<template>
   <form @submit.prevent="createOrder">
        <div>
          email
          <input type="email" v-model="order.email">
        </div>
        <div>Amt
          <input type="number" v-model.number="order.amt">
        </div>
        <div>
          ItemDesc
          <input type="text" v-model="order.itemDesc">
        </div>
        <button type="submit">送出</button>
      </form>
</template>

<script>

export default {
	name: 'OrderCreate',
	data() {
		return {
			order: {
				email: '',
				amt: 100,
				itemDesc: '商品名稱'
			}
		};
	},

	created() {

	},
	methods: {
		async createOrder() {
			try {
				this.isLoading = true;
				const resData = await this.$api.createOrder(this.order);
				if (!resData.data || resData.status !== 'success') {
					throw new Error('建立訂單失敗');
				}
				this.$router.push({ path: `/order/${resData.data.merchantOrderNo}/payment` });
			} catch (error) {
				this.$toasted.show(error.message);
				this.isLoading = false;
			}
		}
	}
};
</script>

